<template>
<div class="about">
    <div class="phone" @click="clickme()">{{ mobile }}</div>
    <swiper
        :direction="'vertical'"
        :slidesPerView="slideView"
        :spaceBetween="30"
        :mousewheel="true"
        :pagination="{
        clickable: true
        }"
        :modules="modules"
        class="mySwiper swiper seoulSwiper"
    >
        <swiper-slide class="swiper-slide">
            <div class="SimgBox website">
                <img src="../assets/styleGuide/PBLTF64.png" alt="slide">
            </div>
        </swiper-slide>
        <swiper-slide class="swiper-slide">
            <div class="SimgBox website">
                <a href="../../888.N-Seoul/index.html" target="_blank">
                    <img src="../assets/logo/logo.png" alt="slide" class="webLogo">
                    <p class="cheakWeb">CHEAK WEB SITE</p>
                </a>
            </div>
        </swiper-slide>
    </swiper>
</div> <!-- temple div-->
</template>

<script>
// Import Swiper Vue.js components
import { Swiper, SwiperSlide } from 'swiper/vue'
// import required modules
import { Mousewheel, Pagination } from 'swiper'

export default {
    components: {
        Swiper,
        SwiperSlide
    },
    setup() {
        return {
        modules: [Mousewheel, Pagination]
        }
    },
    data () {
        return {
            slideView: 1,
            mobile: 'mobile'
        }
    },
    methods: {
        clickme () {
            if (this.slideView === 1) {
                this.slideView = 2
            } else {
                this.slideView = 1
            }
        }
    }
}
</script>
