<template>
<div class="about">
    <div class="phone" @click="clickme()">{{ mobile }}</div>
    <swiper
        :direction="'vertical'"
        :slidesPerView="slideView"
        :spaceBetween="30"
        :mousewheel="true"
        :pagination="{
        clickable: true
        }"
        :modules="modules"
        class="mySwiper swiper electricSwiper"
    >
        <swiper-slide class="swiper-slide">
            <div class="SimgBox website">
                <img src="../assets/styleGuide/PBLTF61.png" alt="slide">
            </div>
        </swiper-slide>
        <swiper-slide class="swiper-slide">
            <div class="SimgBox">
                <img src="../assets/styleGuide/slide1.png" alt="slide">
            </div>
        </swiper-slide>
        <swiper-slide class="swiper-slide">
            <div class="SimgBox">
                <img src="../assets/styleGuide/slide2.png" alt="slide">
            </div>
        </swiper-slide>
        <swiper-slide class="swiper-slide">
            <div class="SimgBox">
                <img src="../assets/styleGuide/slide3.png" alt="slide">
            </div>
        </swiper-slide>
        <swiper-slide class="swiper-slide">
            <div class="SimgBox">
                <img src="../assets/styleGuide/slide4.png" alt="slide">
            </div>
        </swiper-slide>
        <swiper-slide class="swiper-slide">
            <div class="SimgBox">
                <img src="../assets/styleGuide/slide5.png" alt="slide">
            </div>
        </swiper-slide>
        <swiper-slide class="swiper-slide">
            <div class="SimgBox">
                <img src="../assets/styleGuide/slide6.png" alt="slide">
            </div>
        </swiper-slide>
        <swiper-slide class="swiper-slide">
            <div class="SimgBox website">
                <a href="../../900.Electric/index.html" target="_blank">
                    <img src="../assets/logo/logo_electric.png" alt="slide" class="webLogo">
                    <p class="cheakWeb">CHEAK WEB SITE</p>
                </a>
            </div>
        </swiper-slide>
    </swiper>
</div>
</template>
<script>
// Import Swiper Vue.js components
import { Swiper, SwiperSlide } from 'swiper/vue'
// import required modules
import { Mousewheel, Pagination } from 'swiper'

export default {
    created () {
        window.addEventListener('load', this.clickme.click)
    },
    unmounted () {
        window.removeEventListener('load', this.clickme.click)
    },
    components: {
        Swiper,
        SwiperSlide
    },
    setup() {
        return {
        modules: [Mousewheel, Pagination]
        }
    },
    data () {
        return {
            slideView: 1,
            mobile: 'mobile'
        }
    },
    methods: {
        clickme () {
            if (this.slideView === 1) {
                this.slideView = 2
            } else if (this.slideView === 2) {
                this.slideView = 1
            }
        }
    }
}
</script>
