            </div>
        </swiper-slide>
        <swiper-slide class="swiper-slide">
            <div class="SimgBox">
                <img src="../assets/styleGuide/slide8.png" alt="slide">
            </div>
        </swiper-slide>
        <swiper-slide class="swiper-slide">
            <div class="SimgBox">
