<script>
// Import Swiper Vue.js components
import { Swiper, SwiperSlide } from 'swiper/vue'
// import required modules
import { Mousewheel, Pagination } from 'swiper'

export default {
    components: {
        Swiper,
        SwiperSlide
    },
    setup() {
        return {
        modules: [Mousewheel, Pagination]
        }
    },
    data () {
        return {
            slideView: 1,
            mobile: 'mobile'
        }
    },
    methods: {
        mediaClick () {
            window.addEventListener('click', function () {
                this.mediaScreen.click()
            })
        },
        mediaScreen () {
            if (matchMedia.apply('screen and (max-width: 576px)')) {
                this.slideView = 2
            }
        },
        clickme () {
            if (this.slideView === 1) {
                this.slideView = 2
            } else {
                this.slideView = 1
            }
        }
    }
}
</script>
